<template>
	<view v-if="isshow" class="flex-column mx-center sx-center"
		style="height: 100%;z-index: 9999;position: fixed;top: 0;left: 0;right: 0; margin: 0 auto;background-color: rgba(0,0,0,0.7);">

		<view class="flex-column mx-center sx-center" style="padding: 20rpx; border: 2rpx solid gray;">
			<view class="flex-row mx-center sx-center">
				<input v-model="searchAddress" style="flex:1 1 auto;background-color: #FFFFFF;" placeholder="搜索关键字" />
				<view @tap="searchKeyWord" style="margin: 20rpx 10rpx; border: 1px solid gray;">搜索</view>
			</view>
			<map id="map1" ref="map1" style="width: 100%; height: 300px;" :latitude="latitude" :longitude="longitude"
				:markers="markers" @tap="tap">
			</map>
		</view>
	</view>

</template>

<script>
	export default {
		props: ['isshow'],
		mounted(param) {


			uni.getLocation({
				type: 'wgs84',
				geocode: true,
				success: (res) => {
					// console.log(JSON.stringify(res.address)   );
					// let {latitude,longitude,speed,accuracy,altitude,address} = res;
					// console.log({
					// 	latitude,longitude,speed,accuracy,altitude,address
					// })
					this.latitude = res.latitude;
					this.longitude = res.longitude;
					this.markers[0].latitude = res.latitude;
					this.markers[0].longitude = res.longitude;

					//地图跳转到 指定经纬度 的页面，有导航按钮，切换到高德地图app
					// uni.openLocation({   
					//             latitude: res.latitude,
					//             longitude: res.longitude,
					//             success: function (res) {
					// 				console.log(JSON.stringify(res));
					//                 console.log('success');
					//             }
					//         });
				},
				fail(res) {
					console.log(res);
				}
			});



		},
		data() {
			return {

				searchAddress: '',
				title: 'map',
				latitude: 39.909,
				longitude: 116.39742,
				markers: [{
						id: 1,
						title: '123',
						latitude: 23.043594,
						longitude: 113.160704,
						iconPath: '/static/icon/icon1.png'
					},

				],
			}
		},
		methods: {

			searchKeyWord() {
				let that = this;
				console.log('---' + this.searchAddress);
				let mapContext = uni.createMapContext("map1");
				console.log(mapContext);
				uni.chooseLocation({
					keyword: this.searchAddress,
					success: (res) => {
						console.log(res) //name address  latitude  longitude
						mapContext.moveToLocation({
							longitude: res.longitude,
							latitude: res.latitude,
							success: (e) => {
								// console.log(JSON.stringify(e));
								console.log("===========");
								that.markers[0].longitude = res.longitude;
								that.markers[0].latitude = res.latitude;
							},
							fail:(e)=>{
								console.log("==========23=");
								console.log(e);
							}
						})
					}
				})




			},

			tap(e) {
				console.log(e);
				this.markers[0].latitude = e.detail.latitude;
				this.markers[0].longitude = e.detail.longitude;
			}


		}
	}
</script>

